@charset "UTF-8";

@font-face {
  font-family: "cobblestone";
  src:url("../fonts/cobblestone.eot");
  src:url("../fonts/cobblestone.eot?#iefix") format("embedded-opentype"),
    url("../fonts/cobblestone.woff") format("woff"),
    url("../fonts/cobblestone.ttf") format("truetype"),
    url("../fonts/cobblestone.svg#cobblestone") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "cobblestone" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]{
  position: relative;
  top:2px;
}

